  color: #000000;
  outline: none;
  font-size: 18px;
  text-align: center;
}

.form-submit {
  display: inline-block;
  border: 2px solid #F44336;
  padding: .2rem .6rem;
  color: #F44336;
  background: 0 0;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 20px;
  height: auto;
  text-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-decoration: none;
}

.form-submit:hover {

  border: 2px solid #F44336;
  color: #fff;
  background: #F44336;
}